.header__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 17px 0 16px 0;
}

.header__start {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.header__end {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.header__logo {
    margin-left: 54px;
}

.header__custom-toggle {
    user-select: none;
    position: relative;
    width: 131px;
    height: 37px;
    margin-left: 30px;
    font-size: 14px;
    overflow: hidden;
    font-family: DemiBold, sans-serif;

    label {
        display: block;
        width: 131px;
        height: 37px;
        border-radius: $radius-big;
        background-color: $middle-gray;
        cursor: pointer;

        &:active:after {
            width: 10px;
        }

        &:after {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            transition: all 0.5s;
        }

        .text {
            display: flex;
            align-items: center;
            justify-content: space-around;
            width: 100%;
            height: 100%;
            user-select: none;

            .on {
                width: 31px;
                height: 15px;
                text-align: center;
                color: $white;
                padding: 8px 12px;
                background-color: $purple;
                border-radius: 34px;
            }

            .off {
                color: $text-secondary;
            }
        }
    }

    input {
        display: none;

        &:checked {
            & + label {
                background-color: $purple;

                &:after {
                    right: 3px;
                    left: unset;
                    background-color: $text-secondary;
                    transform: translateX(3px);

                    .off {
                        color: $white;
                    }
                }
            }
        }
    }
}

.header__nav {
    display: flex;
    align-content: center;
    justify-content: space-around;
    max-width: 662px;

    & a {
        font-family: Medium, sans-serif;
        margin-right: 32px;
        color: $black;
        text-decoration: none;
        outline: none;
        font-weight: 500;
        font-size: 16px;

        &:hover {
            text-decoration: underline;
        }
    }
}

.header__info {
    margin-right: 38px;
    font-family: Bold, sans-serif;
    font-size: 16px;
    font-weight: 700;
    color: $black;
}

.header__menu-button {
    box-sizing: border-box;
    z-index: 2;
    margin-right: 36px;
    width: 32px;
    height: 32px;
    cursor: pointer;

    svg {
        transition: .2s;
        stroke: $black;

        g {
            &:first-child {
                transition: opacity 0s .2s;
                opacity: 1;

                line {
                    transform: translateY(0);
                    transition: transform .2s .2s;
                }
            }

            &:last-child {
                transition: opacity 0s .2s;
                opacity: 0;

                line {
                    transform: rotate(0deg);
                    transition: transform .2s;
                    transform-origin: center;
                }
            }
        }
    }

    &.-menu-open svg {
        stroke: $black;

        g {
            &:first-child {
                opacity: 0;

                line {
                    transition: transform .2s;

                    &:first-child {
                        transform: translateY(7px);
                    }

                    &:last-child {
                        transform: translateY(-7px);
                    }
                }
            }

            &:last-child {
                opacity: 1;

                line {
                    transition: transform .2s .2s;

                    &:first-child {
                        transform: rotate(45deg);
                    }

                    &:last-child {
                        transform: rotate(-45deg);
                    }
                }
            }
        }
    }
}
